body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

nav {
  font-family: 'Roboto', sans-serif;
  background: #1d1c1c; }
  nav ul {
    margin: 0;
    padding: 0;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-around; }
    nav ul li {
      cursor: pointer;
      list-style-type: none; }
      nav ul li a {
        color: white;
        text-decoration: none;
        font-size: 1.1rem; }
        nav ul li a.active, nav ul li a:hover {
          border-bottom: 2px solid rgba(201, 201, 83, 0.924);
          padding-bottom: 3px;
          color: rgba(201, 201, 83, 0.924); }

/* Home Page */
.home {
  width: 100%; }
  .home .hero-section {
    display: flex;
    height: 500px;
    background: white; }
    .home .hero-section .section-left {
      flex: 1 1;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 1.7rem;
      background: rgba(201, 201, 83, 0.924); }
      .home .hero-section .section-left .left-text-box {
        color: black; }
        .home .hero-section .section-left .left-text-box p:nth-child(1) {
          font-size: 1rem;
          display: inline-block;
          border-bottom: 1px solid black; }
        .home .hero-section .section-left .left-text-box p:nth-child(2) {
          font-weight: 700;
          padding-top: 20px; }
    .home .hero-section .section-right {
      display: flex;
      align-items: center;
      justify-content: center;
      flex: 1 1; }
      .home .hero-section .section-right .right-text-box {
        height: 70%;
        width: 70%; }
        .home .hero-section .section-right .right-text-box img {
          max-width: 100%;
          max-height: 100%;
          box-shadow: 4px 6px 15px 0px rgba(0, 0, 0, 0.75); }
  .home .intro-section {
    display: flex;
    width: 100%;
    height: 500px; }
    .home .intro-section .intro-left {
      flex: 1 1;
      display: flex;
      align-items: center;
      justify-content: center;
      -webkit-transform: rotate(-180deg);
              transform: rotate(-180deg); }
      .home .intro-section .intro-left .left-text-box {
        height: 70%;
        width: 70%; }
        .home .intro-section .intro-left .left-text-box img {
          max-width: 100%;
          max-height: 100%;
          box-shadow: 4px 6px 15px 0px rgba(0, 0, 0, 0.75); }
    .home .intro-section .intro-right {
      background: rgba(201, 201, 83, 0.924);
      flex: 1 1;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 1.7rem;
      -webkit-transform: rotate(-180deg);
              transform: rotate(-180deg); }
      .home .intro-section .intro-right .right-text-box p:nth-child(1) {
        font-size: 1rem;
        display: inline-block;
        border-bottom: 1px solid black; }
      .home .intro-section .intro-right .right-text-box p:nth-child(2) {
        font-weight: 700;
        padding-top: 20px; }

.wrapper {
  padding-top: 10px;
  background: black;
  color: white; }
  .wrapper .about {
    width: 80%;
    margin: 0 auto; }
    .wrapper .about h3 {
      margin: 0;
      font-size: 1.5rem; }
    .wrapper .about .about-section {
      display: flex;
      align-items: center; }
      .wrapper .about .about-section .about-text {
        flex: 1 1;
        border-top: 5px solid white;
        border-bottom: 5px solid white; }
        .wrapper .about .about-section .about-text p {
          margin: 0.5rem; }
          .wrapper .about .about-section .about-text p span {
            color: rgba(201, 201, 83, 0.924);
            text-align: end; }
    .wrapper .about .about-img {
      flex: 1 1;
      height: 200px;
      width: 500px;
      border: 10px solid white; }
      .wrapper .about .about-img img {
        height: 100%;
        width: 100%;
        object-fit: cover; }
    .wrapper .about .experience-section {
      display: flex;
      margin: 20px auto; }
      .wrapper .about .experience-section .experience {
        flex-grow: 1; }
      .wrapper .about .experience-section .skills {
        flex-grow: 1; }
      .wrapper .about .experience-section .featured {
        flex-grow: 1; }
    .wrapper .about .personal-section {
      display: flex;
      padding-bottom: 20px; }
      .wrapper .about .personal-section .personal {
        flex: 1 1;
        margin-right: 20px; }
      .wrapper .about .personal-section .personal-img {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex: 2 1; }
        .wrapper .about .personal-section .personal-img .img-box {
          height: 140px;
          width: 140px;
          border: 10px solid white;
          background: rgba(145, 11, 11, 0.801); }
          .wrapper .about .personal-section .personal-img .img-box img {
            height: 100%;
            width: 100%;
            object-fit: contain; }

.pix {
  min-height: 80vh;
  width: 100%;
  background: #8a6868; }
  .pix .hero-section {
    width: 80%;
    margin: 0 auto;
    padding-top: 20px; }
  .pix .image-results {
    border: 1px solid blue; }

/* Footer */
footer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 200px;
  width: 100%;
  background: #1d1c1c; }
  footer p {
    color: white; }

